import React from 'react';
import Select from 'react-select';

const JoinPresenter = ({ onChange, onSubmit, name, id, email, password, password2, pnum }) => {
  const emailOptions = [
    { value: '', label: '' },
    { value: '@naver.com', label: 'naver.com' },
    { value: '@gmail.com', label: 'gmail.com' },
    { value: '@hanmail.com', label: 'hanmail.com' },
  ];

  const onMainCheckboxChange = (e) => {
    if (e.target.checked) {
      const checkboxs = document.getElementsByClassName('join_checkbox');
      for (let checkbox of checkboxs) checkbox.checked = true;
    } else {
      const checkboxs = document.getElementsByClassName('join_checkbox');
      for (let checkbox of checkboxs) checkbox.checked = false;
    }
  };

  return (
    <div style={{ minWidth: 1700 }} className="flex flex-col items-center font-noto font-bold">
      <div className="mt-28 text-3xl font-bold font-mont">X-RT  </div>
      <div style={{ width: 800 }} className="border-b-4 border-solid mt-3 text-2xl pb-3">
        
      </div>
      <div className="flex justify-between pr-2 py-8 pl-5 text-lg border-b border-gray-400 border-solid" style={{ width: 800 }}>
        <div className="flex">
          <div className="mr-3">
            <input type="checkbox" onChange={onMainCheckboxChange} style={{ transform: 'scale(1.5)' }} />
          </div>
          <label htmlFor="">  .</label>
        </div>
        {/* <i className="fas fa-chevron-down"></i> */}
      </div>
      <div
        className="flex justify-between items-center pr-2 py-7 pl-5 text-base border-b border-gray-400 border-solid font-normal"
        style={{ width: 800 }}
      >
        <div className="flex">
          <div className="mr-3">
            <input className="join_checkbox necessary_checkbox" type="checkbox" style={{ transform: 'scale(1.5)' }} />
          </div>
          <label htmlFor="">
            <span className="text-mainRed">[]</span>  
          </label>
        </div>
        <i className="fas fa-chevron-down"></i>
      </div>
      <div
        className="flex justify-between items-center pr-2 py-7 pl-5 text-base border-b border-gray-400 border-solid font-normal"
        style={{ width: 800 }}
      >
        <div className="flex">
          <div className="mr-3">
            <input className="join_checkbox necessary_checkbox" type="checkbox" style={{ transform: 'scale(1.5)' }} />
          </div>
          <label htmlFor="">
            <span className="text-mainRed">[]</span>  
          </label>
        </div>
        <i className="fas fa-chevron-down"></i>
      </div>
      <div
        className="flex justify-between items-center pr-2 py-7 pl-5 text-base border-b border-gray-400 border-solid font-normal"
        style={{ width: 800 }}
      >
        <div className="flex">
          <div className="mr-3">
            <input className="join_checkbox necessary_checkbox" type="checkbox" style={{ transform: 'scale(1.5)' }} />
          </div>
          <label htmlFor="">
            <span className="text-mainRed">[]</span>     
          </label>
        </div>
        <i className="fas fa-chevron-down"></i>
      </div>
      <div
        className="flex justify-between items-center pr-2 py-7 pl-5 text-base border-b border-gray-400 border-solid font-normal"
        style={{ width: 800 }}
      >
        <div className="flex">
          <div className="mr-3">
            <input className="join_checkbox optional_checkbox" type="checkbox" style={{ transform: 'scale(1.5)' }} />
          </div>
          <label htmlFor="">
            <span className="text-black">[]</span>     
          </label>
        </div>
        <i className="fas fa-chevron-down"></i>
      </div>
      <div style={{ width: 800 }} className="text-mainRed font-normal pt-5">
        <i className="fas fa-exclamation-circle mr-2"></i> 14   .
      </div>

      <div style={{ width: 800 }}>
        <form className="w-full pt-14" onSubmit={onSubmit}>
          <div className="border-b border-solid border-gray-400 pb-12">
            <div className="border-b-4 border-solid text-2xl pb-3 mb-14 w-full"></div>
            <div className="flex text-lg mb-7 mx-5 ">
              <div style={{ width: 130 }}>
                <label htmlFor=""></label>
              </div>
              <div className="border-b border-solid">
                <input
                  type="text"
                  name="name"
                  value={name}
                  onChange={onChange}
                  style={{ width: 620, paddingBottom: 8 }}
                  placeholder=", ,  ."
                />
              </div>
            </div>
            <div className="flex text-lg mb-7 mx-5">
              <div style={{ width: 130 }}>
                <label htmlFor=""></label>
              </div>
              <div className="border-b border-solid">
                <input
                  type="text"
                  name="id"
                  value={id}
                  onChange={onChange}
                  style={{ width: 620, paddingBottom: 8 }}
                  placeholder="  (,  3~20)"
                />
              </div>
            </div>
            <div className="flex text-lg mb-7 mx-5">
              <div style={{ width: 130 }}>
                <label htmlFor=""></label>
              </div>
              <div className="border-b border-solid">
                <input
                  type="password"
                  name="password"
                  value={password}
                  onChange={onChange}
                  style={{ width: 620, paddingBottom: 8 }}
                  placeholder=" (, ,  2  10~20)"
                />
              </div>
            </div>
            <div className="flex text-lg mb-7 mx-5">
              <div style={{ width: 130 }}>
                <label htmlFor=""> </label>
              </div>
              <div className="border-b border-solid">
                <input
                  type="password"
                  name="password2"
                  value={password2}
                  onChange={onChange}
                  style={{ width: 620, paddingBottom: 8 }}
                  placeholder=" "
                />
              </div>
            </div>
            <div className="flex text-lg mb-7 mx-5">
              <div style={{ width: 130 }}>
                <label htmlFor=""></label>
              </div>
              <div className="border-b border-solid">
                <input
                  type="text"
                  name="email"
                  value={email}
                  autoComplete="off"
                  onChange={onChange}
                  style={{ width: 300, paddingBottom: 8 }}
                  placeholder="  "
                />
              </div>
              <div style={{ width: 300, marginLeft: 20 }}>
                <Select name="emailHost" options={emailOptions} defaultValue={emailOptions[0]} onChange={onChange} />
              </div>
            </div>
            <div className="flex text-lg mb-7 mx-5">
              <div style={{ width: 130 }}>
                <label htmlFor=""> </label>
              </div>
              <div className="border-b border-solid">
                <input
                  type="text"
                  name="pnum"
                  value={pnum}
                  onChange={onChange}
                  style={{ width: 620, paddingBottom: 8 }}
                  placeholder="  '-' "
                />
              </div>
            </div>
          </div>

          <div className="w-full flex justify-center pt-10">
            <button className="h-16 w-52 mr-4 bg-gray-400 text-white font-bold hover:bg-gray-600 transition"> </button>
            <button className="h-16 w-52 bg-black text-white font-bold"></button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default JoinPresenter;
